import React, { useContext, useEffect, useRef, useState } from 'react'
import giftImg from "../assets/gift.png"
import adImg from "../assets/ad.png"
import { Users } from '../dummyData'
import Online from './Online'
import axios from "axios"
import {Link} from 'react-router-dom'
import { userService } from '../services/user.service'
import { AuthContext } from '../context/AuthContext'
import AddIcon from '@mui/icons-material/Add';
import DoneIcon from '@mui/icons-material/Done';
import { follow, sendNotification, unfollow } from '../context/AuthActions'
import { io } from 'socket.io-client'
import { userSocket } from '../services/socket'

export default function Rightbar({ user }) {

    const HomeRightBar = () => {
        const [onlinefriends, setOnlineFriends] = useState(null)
        const {user: loggedInUser} = useContext(AuthContext)
    
        useEffect(()=>{
            // if(loggedInUser){
            //     getOnlineFriends(loggedInUser)
            // }
            userSocket.on('getUsers', data => {
                console.log('new user entered');
                let online = data.filter(u => u.userId !== loggedInUser._id && loggedInUser?.following.includes(u.userId))
                setOnlineFriends(online)
            })
        },[loggedInUser.following])
    
        // const getOnlineFriends = async(user) => {
        // }

      
        return (
            <>
                <div className="birthdayContainer">
                    <img className="birthdayImg" src={giftImg} alt="" />
                    <span className="birthdayText">
                        <b>Pola Foster</b> and <b>3 other friends</b> have a birhday today.
                    </span>
                </div>
                <img className="rightbarAd" src={adImg} alt="" />
                <h4 className="rightbarTitle">Online Friends</h4>
                <ul className="rightbarFriendList">
                    {onlinefriends && onlinefriends?.map(friend =>
                        <Online key={friend.userId} userId={friend.userId} />
                    )}
                </ul>
            </>
        )
    }

    const ProfileRightBar = ({user}) => {
        const [friends, setFriends] = useState(null)
        const [isFollowed, setIsFollowed] = useState(null)
        const {dispatch} = useContext(AuthContext)
        const {user: loggedInUser} = useContext(AuthContext)

        const socket = useRef()

    
        useEffect(()=>{
          socket.current = userSocket
          if(user?._id){
              getFriends(user)
              setIsFollowed(user?.followers?.includes(loggedInUser?._id))
          }
          
        },[])

        const getFriends = async(user) => {
            const fetchedFriends = await userService.getUserFriends(user)
            setFriends(fetchedFriends)
        }
    
        const handleFollow = async(userId) => {
            try{
                if(isFollowed){
                    // await userService.unfollowUser(userId, loggedInUser._id)
                    await unfollow(userId, loggedInUser?._id, dispatch)
                } else{
                    // await userService.followUser(userId, loggedInUser._id)
                    await follow(userId, loggedInUser?._id, dispatch)
                    socket?.current.emit('follow', {followerId: loggedInUser._id , followedId: user._id, timeStamp: new Date().toISOString()})

                }

                await sendNotification('newFollows', user._id, { userId: loggedInUser._id, timeStamp: new Date().toISOString() }, dispatch)

                setIsFollowed(prevFollowed => {
                    return !prevFollowed
                })
            }catch(err){
                console.log(err);
            }
        }

       
        return (
            <>
                {user._id !== loggedInUser._id && (
                    <button className='rightbarFollowBtn' onClick={()=>handleFollow(user._id)}>
                        {isFollowed
                        ? <><DoneIcon/> Unfollow</>
                        : <><AddIcon/> Follow</>}
                    </button>
                )}
                <h4 className='rightbarTitle'>
                    User Information
                </h4>
                <div className="rightbarInfo">
                    <div className="rightbarInfoItem">
                        <span className="rightbarInfoKey">City:</span>
                        <span className="rightbarInfoValue">{user?.city}</span>
                    </div>
                    <div className="rightbarInfoItem">
                        <span className="rightbarInfoKey">Occupation:</span>
                        <span className="rightbarInfoValue">{user?.occupation}</span>
                    </div>
                    <div className="rightbarInfoItem">
                        <span className="rightbarInfoKey">Relationship status:</span>
                        <span className="rightbarInfoValue">{user?.relationship === 1 ? 'Single': user?.relationship ===2 ? "Married": 'In Relationship'}</span>
                    </div>
                </div>

                <h4 className='rightbarTitle'>User Friends</h4>
                <div className="rightbarFollowings">
                    {friends && friends?.map(friend =>(
                        <div key={friend._id} className="rightbarFollowing">
                            <Link to={`/profile/${friend._id}`}>
                                <img src={friend.profilePicture || require(`../assets/person/noAvatar.png`)} alt="" className="rightbarFollowingImg" />
                            </Link>
                            <span className='rightbarFollowingName'>{friend.username}</span>
                        </div>)
                    )}
                </div>
            </>
        )
    }

    return (
        <div className='rightbar'>
            <div className="rightbarWrapper">
                {user ? <ProfileRightBar user={user}/> : <HomeRightBar/>}
            </div>
        </div>
    )
}
