import axios from 'axios'
import React, { useRef } from 'react'
import { useNavigate } from 'react-router-dom'
import { userService } from '../services/user.service'

export default function Register() {
    const navigate = useNavigate()

    const email = useRef()
    const username = useRef()
    const password = useRef()
    const passwordAgain = useRef()

    const handleForm = async(ev) => {
        console.log(password.current.value, passwordAgain.current.value);
        ev.preventDefault()
        if(password.current.value !== passwordAgain.current.value){
            password.current.setCustomValidity("Passwords dont match")
        }else{
            const user = {
                username: username.current.value,
                email: email.current.value,
                password: password.current.value
            }
            try{
                await userService.registerUser(user)
                navigate('/login')
            }catch(err){
                console.log(err);
            }

        }
    }

    return (
        <div className='register'>
            <div className="registerWrapper">
                <div className="registerLeft">
                    <h3 className="registerLogo">BeSocial</h3>
                    <span className="registerDesc">
                        Connect with friends and the world around you on BeSocial.
                    </span>
                </div>
                <div className="registerRight">
                    <form className="registerBox" onSubmit={handleForm}>
                        <input ref={username} type="text" className='registerInput' placeholder='Username'/>
                        <input ref={email} minLength="6" type="email" className='registerInput' placeholder='Email'/>
                        <input ref={password} minLength="6" type="password" className='registerInput' placeholder='Password'/>
                        <input ref={passwordAgain} minLength="6" type="password" className='registerInput' placeholder='Enter password again'/>
                        <button className='registerBtn' type='submit'>Sign Up</button>
                        <span className='registerForgot'>Already have an account?</span>
                        <button className="registerRegisterBtn" onClick={()=>{navigate('/login')}}>Log In</button>
                    </form>
                </div>
            </div>
        </div>
    )
}
