import React, { useContext, useEffect, useState } from 'react'

import WorkIcon from '@mui/icons-material/Work';
import FavoriteIcon from '@mui/icons-material/Favorite';
import LocationCityIcon from '@mui/icons-material/LocationCity';
import { Link } from 'react-router-dom'
import { AuthContext } from '../context/AuthContext';
import { follow, sendNotification, unfollow } from '../context/AuthActions';

export default function SearchedFriend({user}) {

    const {user:loggedInUser, dispatch} = useContext(AuthContext)
    const [isFollowed, setIsFollowed] = useState(null)

    useEffect(()=>{
        if(user?.followers?.includes(loggedInUser?._id)){
            setIsFollowed(true)
        }else{
            setIsFollowed(false)
        }
    },[user])

    const handleFollow = async(userId) => {
        try{
            if(isFollowed){
                // await userService.unfollowUser(userId, loggedInUser._id)
                await unfollow(userId, loggedInUser?._id, dispatch)
            } else{
                await follow(userId, loggedInUser?._id, dispatch)
                // await userService.followUser(userId, loggedInUser._id)
                // socket?.current.emit('follow', {followerId: loggedInUser._id , followedId: user._id, timeStamp: new Date().toISOString()})

            }

            // await sendNotification('newFollows', user._id, { userId: loggedInUser._id, timeStamp: new Date().toISOString() }, dispatch)

            setIsFollowed(prevFollowed => {
                return !prevFollowed
            })
        }catch(err){
            console.log(err);
        }
    }

    return (
        <>
            <div className='findUser'>
                <Link to={`/profile/${user?._id}`}>
                    <img className='userImg' src={user?.profilePicture || require('../assets/person/noAvatar.png')} alt="" />
                </Link>
                <div className="details">

                    <div className="userDetails">
                        <Link to={`/profile/${user?._id}`} className="nameLink">
                            <p className='username'>{user?.username}</p>
                        </Link>
                        <p className='occupation'><WorkIcon className='icon' /> {user?.occupation}</p>
                        <p className='other'><FavoriteIcon className='icon' />{user?.relationship === 2 ? 'Single' : 'In Relationship'}</p>
                        <p className='other'><LocationCityIcon className='icon' /> {user?.city}</p>
                    </div>
                    <div className="userBtns">
                        <button onClick={()=>handleFollow(user._id)}>{isFollowed ? 'Unfollow' : 'Follow'}</button>
                    </div>
                </div>
            </div>

        </>
    )
}
