import { io } from "socket.io-client";
import { AuthContext } from "../context/AuthContext";
import React, { useContext, useEffect, useRef, useState } from 'react'

const baseUrl = process.env.NODE_ENV === 'production' ? '' : '//localhost:8800'


export const userSocket = io(baseUrl)


export default function Socket() {
    const {user, dispatch} = useContext(AuthContext)

    useEffect(()=>{
        userSocket.on('getUsers', data=>{
            console.log(data);
        })
        
        userSocket?.on("likeNotification" , async data =>{
            await dispatch({type: "ADD_NOTIFICATION", payload: {type: 'likes', details: data}})
            console.log('data');
            // console.log(user);
        })

    },[])
  return (
    <div>socket</div>
  )

}

