import React, { useEffect, useState } from 'react'
import { conversationService } from '../services/conversation.service'
import { userService } from '../services/user.service'

export default function ChatOnline({ onlineUsers, user, setCurrentChat }) {

    const [friends, setFriends] = useState(null)
    const [onlineFriends, setOnlineFriends] = useState(null)

    useEffect(() => {
        if(user){
            getFriends(user)
        }
    }, [user._id])

    useEffect(() => {
        if(friends){
            setOnlineFriends(friends.filter(friend => onlineUsers?.includes(friend._id)))
        }
    }, [friends, onlineUsers])

    const getFriends = async (user) => {
        const friends = await userService.getUserFriends(user)
        setFriends(friends)
    }

    const handleClick = async(friend) => {
        try {
            const conversation = await conversationService.findConversationByIds(user?._id, friend?._id)
            console.log(conversation);
            setCurrentChat(conversation)
        } catch (err) {
            console.log(err);
        }
    }

    return (
        <div className='chatOnline'>
            <h2>Online now:</h2>
            {onlineFriends?.map(friend => (
                <div className="chatOnlineFriend" onClick={()=>handleClick(friend)}>
                    <div className="chatOnlineImgContainer">
                        <img className='chatOnlineImg' src={friend?.profilePicture || require('../assets/person/noAvatar.png')} alt="" />
                        <div className="chatOnlineBadge"></div>
                    </div>
                    <span className="chatOnlineName">{friend?.username}</span>
                </div>
            ))}
        </div>
    )
}
