import React, { useContext, useEffect, useState } from 'react'
import Share from './Share'
import Post from './Post'
import axios from "axios"
import { AuthContext } from '../context/AuthContext'
import { postService } from '../services/post.service'
import { textAlign } from '@mui/system'

export default function Feed({ userId, newImg }) {
    const [posts, setPosts] = useState([])
    const {user} = useContext(AuthContext)
    useEffect(() => {
        if(user){
            getPosts(userId)
        } 

    }, [userId, user?._id, newImg])

    const getPosts = async (userId) => {
        let posts
        if (userId) {
            posts = await postService.getProfilePosts(userId)
        } else {
            posts = await postService.getTimeline(user._id)
        }
        setPosts(posts)
    }

    // 383826078
    return (
        <div className='feed'>
            <div className="wrapper">
                {userId === user._id && <Share posts={[...posts]} setPosts={setPosts}/> || !userId && <Share posts={[...posts]} setPosts={setPosts}/>}
                {posts.length ? posts?.map(p =>
                    <Post key={p._id} postProp={p} />
                ) : <h2 className='noPosts'>No posts yet</h2> }
            </div>
        </div>
    )
}


