const AuthReducer = (state, action ) => {
    switch(action.type){
        case "LOGIN_START" :
            return {
                user:null,
                isFetching: true,
                error: false,
            };
        case "LOGIN_SUCCESS" :
            return {
                user:action.payload,
                isFetching: false,
                error: false,
            };
        case "LOGIN_FAILURE" :
            return {
                user:null,
                isFetching: false,
                error: action.payload,
            };
        case "SET_USER" :
            return {
                ...state,
                user: {...action.payload}
            };
        case "SET_USER_FRIENDS" :{
            return {
                ...state,
                userFriends:[...state.userFriends, ...action.payload]
            };
        }
        case "FOLLOW" :
            return {
                ...state,
                user: {
                    ...state.user, 
                    following: [...state.user.following, action.payload]
                }
            };
        case "UNFOLLOW" :
            return {
                ...state,
                user: {
                    ...state.user, 
                    following: state.user.following.filter(id => id === action.payload)
                }
            };
        case "ADD_NOTIFICATION" :{
                console.log( [...state.user.notifications[action.payload.type], action.payload.details]);
                return {
                    ...state,
                    user: {
                        ...state.user, 
                        notifications: {
                            ...state.user.notifications,
                            [action.payload.type]: [...state.user.notifications[action.payload.type], action.payload.details]
                        }
                    }
                }
        };
        case "REMOVE_NOTIFICATION" :{
         
                return {
                    ...state,
                    user: {
                        ...state.user, 
                        notifications: {
                            ...state.user.notifications,
                            [action.payload.type]: state.user.notifications[action.payload.type]?.filter(obj => obj.userId !== action.payload.details.userId)
                        }
                    }
                }
        };
        case "CLEAR_NOTIFICATIONS" :{
            console.log(action.payload);
                return {
                    ...state,
                    user: {
                        ...state.user, 
                        notifications: {
                            ...state.user.notifications,
                            [action.payload]: []
                        }
                    }
                }
        };
        case "SET_PROFILE_PICTURE" :{
            console.log(action.payload);
                return {
                    ...state,
                    user: {
                        ...state.user, 
                        profilePicture: action.payload 
                    }
                }
        };
        case "SET_COVER_PICTURE" :{
            console.log(action.payload);
                return {
                    ...state,
                    user: {
                        ...state.user, 
                        coverPicture: action.payload 
                    }
                }
        };

            default:
                return state
            
    }
}

export default AuthReducer