import React, { useContext, useEffect, useState } from 'react'
import { postService } from '../services/post.service'
import { userService } from '../services/user.service'
import { format } from 'timeago.js'
import { markAsRead } from '../context/AuthActions'
import { AuthContext } from '../context/AuthContext'
export default function Notifications({ notifications, type, isShown }) {

    const [readyNotifications, setReadyNotifications] = useState([])

    const { user, dispatch } = useContext(AuthContext)

    useEffect(() => {
        // console.log(type);

        if (notifications) {
            getData()
        }
    }, [notifications])

    const getData = async () => {
            const miniNotifications = await Promise.all((notifications?.map(async notification => {
                const user = await userService.getUser(notification?.userId)
                switch (type) {
                    case "likes":
                        const post = await postService.getPost(notification?.postId)
                        return {
                            username: user?.username,
                            userImg: user?.profilePicture,
                            postText: post?.desc,
                            timeStamp: notification?.timeStamp
                        }
                        break;

                    case "newFollows":
                        return {
                            username: user?.username,
                            userImg: user?.profilePicture,
                            timeStamp: notification?.timeStamp
                        }

                    case "messages":
                        return {
                            username: user?.username,
                            userImg: user?.profilePicture,
                            text: notification?.text
                        }

                        

                    default:
                        break;
                }
            }
        )))
        // console.log(miniNotifications);
        console.log(type, 'notifications', notifications);
        // const miniNotifications = notifications.map(async n => {
        //     const user = await userService.getUser(n?.userId)

        //     switch (type) {
        //         case "likes":
        //             const post = await postService.getPost(n.postId)
        //             return {
        //                 username: user?.username,
        //                 userImg: user?.profilePicture,
        //                 postText: post?.desc,
        //                 timeStamp: n.timeStamp
        //             }
        //             break;

        //         case "newFollows":
        //             return {
        //                 username: user?.username,
        //                 userImg: user?.profilePicture,
        //                 timeStamp: n.timeStamp
        //             }

        //         default:
        //             break;
        //     }

        // })
        setReadyNotifications(miniNotifications)
    }

    const mark = async () => {
        await markAsRead(dispatch, user?._id, type)
        // console.log(user);
    }

    return (
        <div className='notifications' style={{ display: isShown && notifications.length ? 'flex' : 'none' }}>
            <div className="notificationsTop">
                <p className='notificationTitle'>Notifications</p>
                <span onClick={mark} className='notificationMarkBtn'>Mark All as Read</span>
            </div>
            {readyNotifications?.map(notification => (
                <div key={notification?._id} className="notificationContainer">
                    <div className="notificationItem">
                        <img className='notificationItemImg' src={notification.userImg} alt="" />
                        <div className="notificationItemDetails">
                            <p className='notificationItemText'>{notification.username}  {type === "likes" ? "liked your post" : type === 'newFollows' ? 'followed you' : 'sent a message'} {notification?.postText || notification?.text}</p>
                            <p className='notificationItemTime'>{format(notification?.timeStamp)}</p>
                        </div>
                    </div>
                </div>
            ))}
        </div>
    )
}
