import Home from "./pages/Home";
import Login from "./pages/Login";
import Register from "./pages/Register";
import Profile from "./pages/Profile";
import { Navigate, Route, Routes } from 'react-router-dom'
import { useContext, useEffect } from "react";
import { AuthContext } from "./context/AuthContext";
import { setUser, setUserFriends } from "./context/AuthActions";
import Messenger from "./pages/Messenger";
import { storageService } from "./services/storage.service";
import FindFriends from "./pages/FindFriends";
import { io } from "socket.io-client";
import { userSocket } from "./services/socket";

function App() {

  const { dispatch, user, userFriends } = useContext(AuthContext)
  

  useEffect(()=>{
      userSocket.off('getUsers').on('getUsers', data=>{
          console.log(data);
      })
      
      userSocket.off("likeNotification").on("likeNotification" , async data =>{
          console.log('dispatching like notification');
          await dispatch({type: "ADD_NOTIFICATION", payload: {type: 'likes', details: data}})
      })

      userSocket?.off('followNotification').on("followNotification" , async data =>{
        await dispatch({type: "ADD_NOTIFICATION", payload: {type: 'newFollows', details: data}})
        // console.log(user);
    })
      userSocket?.off('messageNotification').on("messageNotification" , async data =>{
        await dispatch({type: "ADD_NOTIFICATION", payload: {type: 'messages', details: data}})
        // console.log(user);
    })

  },[])

  // useEffect(() => {
  //   const setFriends = async () => {
  //     if (user) {
  //       await setUserFriends(user, dispatch)
  //     }
  //   }
  //   setFriends()
  // }, [user])

  return (
    <Routes>
      <Route path="/" element={user ? <Home /> : <Navigate to='/login' />} />
      <Route path='/login' element={user ? <Navigate to='/' /> : <Login />} />
      <Route path="/register" element={user ? <Navigate to="/" /> : <Register />} />
      <Route path="/messenger" element={user ? <Messenger /> : <Navigate to="/" />} />
      <Route path="/profile/:id" element={ user ? <Profile /> : <Login/>} />
      <Route path="/findfriends" element={ user ? <FindFriends /> : <Login/>} />
    </Routes>
  );
}

export default App;
