import React, { useContext, useState } from 'react'
import { addPostComment } from '../context/AuthActions'
import { AuthContext } from '../context/AuthContext'
import SendIcon from '@mui/icons-material/Send';
export default function
    ({postId, post, setPost}) {
    
    const [comment, setComment] = useState('')
    const {user} = useContext(AuthContext)

    const handleInput = (ev) => {
        let txt = ev.target.value
        setComment(txt)
    }
    const sendComment = async() => {
        const commentObj = {
            timeStamp: new Date().toISOString(),
            userId: user._id,
            // postId,
            text: comment.slice(''),
        }

        setComment('')
        
        //Client
        if(comment){
            setPost({...post, comments: [...post.comments, commentObj]})
        }
        
        //Database
        await addPostComment(post?._id, commentObj)
        
        console.log(comment);
    }

    

    return (
        <div className="commentInput">
            <input type="text" onChange={handleInput} value={comment}/>
            <button onClick={sendComment}><SendIcon className='icon'/></button>
        </div>
    )
}
