import React, { useContext, useEffect, useRef, useState } from 'react'
import img from "../assets/person/1.jpeg"
import PermMediaIcon from '@mui/icons-material/PermMedia';
import LabelIcon from '@mui/icons-material/Label';
import RoomIcon from '@mui/icons-material/Room';
import EmojiEmotionsIcon from '@mui/icons-material/EmojiEmotions';
import CancelIcon from '@mui/icons-material/Cancel';
import { AuthContext } from '../context/AuthContext'
import axios from 'axios'
import { uploadImg } from '../img-upload.service';
import { postService } from '../services/post.service';
import GoogleMap from './GoogleMap';

export default function Share({posts, setPosts}) {
    const emojis=["✌","😂","😝","😁","😱","👉","🙌","🍻","🔥","🌈","☀","🎈","🌹","💄","🎀","⚽","🎾","🏁","😡","👿","🐻","🐶","🐬","🐟","🍀","👀","🚗","🍎","💝","💙","👌","❤","😍","😉","😓","😳","💪","💩","🍸","🔑","💖","🌟","🎉","🌺","🎶","👠","🏈","⚾","🏆","👽","💀","🐵","🐮","🐩","🐎","💣","👃","👂","🍓","💘","💜","👊","💋","😘","😜","😵","🙏","👋","🚽","💃","💎","🚀","🌙","🎁","⛄","🌊","⛵","🏀","🎱","💰","👶","👸","🐰","🐷","🐍","🐫","🔫","👄","🚲","🍉","💛","💚"]

    const { user } = useContext(AuthContext)
    const desc = useRef()
    const [file, setFile] = useState(null)
    const [map, setMap] = useState(false)
    const [center, setCenter] = React.useState(null)
    const [areEmojisShown, setAreEmojisShown] = React.useState(false)

    useEffect(() => {

        function onPositionUpdate(position) {
            var lat = position.coords.latitude;
            var lng = position.coords.longitude;
            setCenter({ lat, lng })
        }


        if (navigator.geolocation) navigator.geolocation.getCurrentPosition(onPositionUpdate);
        else alert("navigator.geolocation is not available");

    }, [map])

    const handleSubmit = async (ev) => {
        ev.preventDefault()
        const newPost = {
            userId: user._id,
            desc: desc.current.value,
            img: file ? file : '',
            map: map ? center : ''
        }

        try {
            const post = await postService.uploadPost(newPost)
            // window.location.reload()
            // console.log(post);
            let newPosts = [post, ...posts]
            console.log(newPosts);
            setPosts(newPosts)
        } catch (err) {
            console.log(err);
        }
    }

    const handleUpload = async(ev) => {
        const file = ev.target.files[0]
        console.log(file);
        const fileUrl = await uploadImg(file)
        console.log(fileUrl.url);
        setFile(fileUrl.url)
    }

    return (
        <div className='share'>
            <div className="wrapper">
                <div className="shareTop">
                    <img className='profileImg' src={user?.profilePicture || require('../assets/person/noAvatar.png')} alt="" />
                    <input ref={desc} type="text" placeholder={`Whats on your mind ${user?.username}?`} className='shareInput' />
                </div>
                <hr />
                {file && (
                    <div className="shareImgContainer">
                        <img src={file} alt="" className="shareImg" />
                        <CancelIcon className="shareCancelImg" onClick={()=>setFile(null)}/>
                    </div>
                )}
                {map && (
                    <div className="shareMapContainer">
                        <GoogleMap center={center} />
                        <CancelIcon className="shareCancelMap" onClick={()=>setMap(false)}/>
                    </div>
                )}
                <form className="shareBottom" onSubmit={handleSubmit}>
                    <div className="shareOptions">
                        <label htmlFor='file' className="option">
                            <PermMediaIcon htmlColor="tomato" className='shareIcon' />
                            <span className='optionText'>Photo / Video</span>
                            <input style={{ display: 'none' }} type="file" id='file' accept='.png,.jpeg,.jpg' onChange={handleUpload} />
                        </label>
                        {/* <div className="option">
                            <LabelIcon htmlColor="blue" className="shareIcon" />
                            <span className="optionText">Tags</span>
                        </div> */}
                        <div className="option" onClick={()=>setMap(true)}>
                            <RoomIcon htmlColor="green" className="shareIcon" />
                            <span className="optionText">Location</span>
                        </div>
                        {window.innerWidth > 800 ? <div className="option emojiOption" onClick={()=>setAreEmojisShown(!areEmojisShown)}>
                            <EmojiEmotionsIcon htmlColor="goldenrod" className="shareIcon" />
                            <span className="optionText">Emotions</span>
                            {areEmojisShown && <div className="emojiBoard">
                                {emojis.map(e =>(
                                    <div className='emoji' onClick={()=>{desc.current.value += e}}>{e}</div>
                                ))}
                            </div>}
                        </div> : ''}
                    </div>
                    <button type='submit' className="shareButton">Share</button>
                </form>
                            <span>{file ? 'File uploaded' : ''}</span>

            </div>
        </div>
    )
}
