import axios from 'axios'
import React, { useContext, useEffect, useRef } from 'react'
import { useNavigate } from 'react-router-dom'
import { login } from '../context/AuthActions'
import { AuthContext } from '../context/AuthContext'
import { userSocket } from '../services/socket'

export default function Login() {
    const navigate = useNavigate()
    const email = useRef()
    const password = useRef()
    const { dispatch, user, isFetching, error } = useContext(AuthContext)
    const socket = useRef()
    
    const handleForm = (ev) => {
        ev.preventDefault()
        const fetchUser = async () => {
            await login({ email: email.current.value, password: password.current.value }, dispatch)
            console.log(error);
            if (user){
                // socket.current = userSocket
                // userSocket.emit('addUser', user._id)
                navigate('/')
            }
        }
        fetchUser()
    }

    return (
        <div className='login'>
            <div className="loginWrapper">
                <div className="loginLeft">
                    <h3 className="loginLogo">BeSocial</h3>
                    <span className="loginDesc">
                        Connect with friends and the world around you on BeSocial.
                    </span>
                </div>
                <div className="loginRight">
                    <form className="loginBox" onSubmit={handleForm}>
                        <input required minLength="6" ref={email} className='loginInput' type='email' placeholder='Email' />
                        <input required minLength="6" ref={password} className='loginInput' type='password' placeholder='Password' />
                        <button className='loginBtn'>{isFetching ? 'Loading...' : 'Log In'}</button>
                        <span className=''>{error ? error : ''}</span>
                        <span className='loginForgot'>Forgot Password?</span>
                        <button className="loginRegisterBtn" onClick={()=>navigate('/register')}>Create new account</button>
                    </form>
                </div>
            </div>
        </div>
    )
}
