import React, { useContext, useEffect, useState } from 'react'

import Topbar from "../components/Topbar";
import Sidebar from "../components/Sidebar";
import Feed from "../components/Feed";
import Rightbar from "../components/Rightbar";
import { useParams } from 'react-router-dom';
import axios from 'axios';
import { userService } from '../services/user.service';
import { uploadImg } from '../img-upload.service';
import { AuthContext } from '../context/AuthContext';
import { setCoverPicture, setProfilePicture } from '../context/AuthActions';

export default function Profile() {

    const { user: loggedInUser, dispatch } = useContext(AuthContext)
    const [user, setUser] = useState({})

    const [isMyProfile, setIsMyProfile] = useState(false)

    const params = useParams()

    useEffect(()=>{
        window.scrollTo(0, 0)
    },[])

    useEffect(() => {
        const getUser = async () => {
            const user = await userService.getUser(params.id)
            setUser(user)
            if (user._id === loggedInUser._id) {
                setIsMyProfile(true)
                setUser(loggedInUser)
            }else{
                setIsMyProfile(false)
            }
        }
        getUser()

    }, [params, loggedInUser])

    const handleUpload = async (ev, type) => {
        const file = ev.target.files[0]
        const fileUrl = await uploadImg(file)
        console.log(type);

        switch (type) {
            case 'profile':
                await setProfilePicture(dispatch, loggedInUser._id, fileUrl.url)
                break;
            case 'cover':
                console.log('coverSelected');
                await setCoverPicture(dispatch, loggedInUser._id, fileUrl.url)
                break;

            default:
                break;
        }

    }
    const [isMenuShown, setIsMenuShown] = useState(false)

    return (
        <>
            <div className="profile">
                <Topbar isMenuShown={isMenuShown} setIsMenuShown={setIsMenuShown} className="profileTopbar" />
                <Sidebar isMenuShown={isMenuShown} className="profileSidebar" />
                <div className="profileRight">
                    <div className="profileRightTop">
                        <div className="profileCover">
                            <label htmlFor={isMyProfile ? "file2" : ''} className='coverLabel'>
                                <div className={isMyProfile ? 'coverImgContainer changeable' : 'coverImgContainer'}>
                                    <img
                                        className="profileCoverImg"
                                        src={user?.coverPicture || require(`../assets/person/noCover.jpg`)} alt=""
                                    />
                                    <span className='changeCoverImgText'>Change Cover</span>
                                </div>
                                <input style={{ display: 'none' }} type="file" id='file2' accept='.png,.jpeg,.jpg' onChange={(ev) => handleUpload(ev, 'cover')} />
                            </label>
                            <label htmlFor={isMyProfile ? "file" : ''} className='profileLabel'>
                                <div className={isMyProfile ? 'profileImgContainer changeable' : 'profileImgContainer'}>
                                    <img
                                        className="profileUserImg"
                                        src={user?.profilePicture || require(`../assets/person/noAvatar.png`)}
                                        alt=""
                                    />
                                    <span className='changeProfileImgText'>Change Picture</span>
                                </div>
                                <input style={{ display: 'none' }} type="file" id='file' accept='.png,.jpeg,.jpg' onChange={(ev) => handleUpload(ev, 'profile')} />
                            </label>
                        </div>
                        <div className="profileInfo">
                            <h4 className="profileInfoName">{user?.username}</h4>
                            <span className="profileInfoDesc">{user?.desc || 'Hello my friends!'}</span>
                        </div>
                    </div>
                    {/* <div className="profileRightBottom"> */}
                    {user._id && <Feed userId={user?._id} newImg={user?.profilePicture} className="feed" />}
                    <Rightbar profile user={user} className="rightBar" />
                    {/* </div> */}
                </div>
            </div>
        </>
    );
}