import React, { useContext, useEffect, useRef, useState} from 'react'
import SearchIcon from '@mui/icons-material/Search';
import PersonIcon from '@mui/icons-material/Person';
import ChatIcon from '@mui/icons-material/Chat';
import NotificationsIcon from '@mui/icons-material/Notifications';
import profileImg from "../assets/person/1.jpeg"
import {Link, useNavigate} from "react-router-dom"
import { AuthContext } from '../context/AuthContext';
import Notifications from './Notifications';
import { io} from 'socket.io-client';
import { userSocket } from '../services/socket';
import MenuIcon from '@mui/icons-material/Menu';

export default function Topbar({users, setUsersToShow, isMenuShown, setIsMenuShown, chat}) {
    const [isNotificationsShown, setIsNotificationsShown] = useState(false)
    const [isFollowsShown, setIsFollowsShown] = useState(false)
    const [isMessagesShown, setIsMessagesShown] = useState(false)
    

    const {user, dispatch} = useContext(AuthContext)
    const socket = useRef()

    const navigate = useNavigate()

    useEffect(()=>{
        socket.current = userSocket
        socket.current.emit('addUser', user._id)
       

        

      

    }, [])

    
//     useEffect(()=>{
//     socket?.current?.on("getUsers" , data =>{
//         // console.log(data);
//     })

//   },[socket.current])

  const check = () => {
    socket.current.emit('like', {likerId:'63f49c30444b8d836da5dc86', likedId:'63f49c30444b8d836da5dc86', timeStamp:'Ac1'})
  }

  const handleSearch = (ev) => {
        
        const txt = ev.target.value
        const reg = new RegExp(txt, 'i')
        const fitUsers = users?.filter(u=>u.username.match(reg))
        setUsersToShow(fitUsers)
  }

  const closeAllNotifications = () => {

  }


    return (
        <div className="topbarContainer">
            {!chat && <button className="hamburgerBtn" onClick={()=>setIsMenuShown(!isMenuShown)}><MenuIcon/></button>}
            <div className="topbarLeft">
                <Link to={'/'} className="logo" style={{textDecoration: 'none'}}>{window.innerWidth < 800 ? 'BeSocial ' :  'BeSocial'}</Link>
            </div>
            <div className="topbarCenter">
                <div className="searchbar">
                    <SearchIcon className="searchIcon"/>
                    <input placeholder="Search for friends, posts or videos" className="searchInput" onChange={handleSearch} onClick={()=>navigate('/findfriends')}></input>
                </div>
            </div>
            <div className="topbarRight">
                {/* <div className="topbarLinks">
                    <span className="topbarLink">Homepage</span>
                    <span className="topbarLink">Timeline</span>
                </div> */}
                <div className="topbarIcons">
                    <div className="topbarIconItem" onClick={()=>setIsFollowsShown(!isFollowsShown)}>
                        <PersonIcon />
                       {user?.notifications.newFollows.length ?
                        <span className="topbarIconBadge">
                            {user?.notifications.newFollows.length}
                        </span> : ''}
                        <Notifications notifications={user?.notifications.newFollows} type="newFollows" isShown={isFollowsShown}/>
                    </div>
                    <div className="topbarIconItem" onClick={()=>setIsMessagesShown(!isMessagesShown)}> 
                        <ChatIcon/>
                         {user?.notifications.messages.length ?
                        <span className="topbarIconBadge">
                            {user?.notifications.messages.length}
                        </span> : ''}
                        <Notifications notifications={user?.notifications.messages} type="messages" isShown={isMessagesShown}/>
                    </div>
                    <div className="topbarIconItem" onClick={()=>setIsNotificationsShown(!isNotificationsShown)}>
                        <NotificationsIcon />
                        {user?.notifications.likes.length ?
                        <span className="topbarIconBadge">
                            {user?.notifications.likes.length}
                        </span> : ''}
                        <Notifications notifications={user?.notifications.likes} type="likes" isShown={isNotificationsShown} />
                    </div>
                </div>
                <Link to={`/profile/${user?._id}`}>
                    <img src={(user?.profilePicture?.includes('../asstes')) ? require(user?.profilePicture) : user.profilePicture || require('../assets/person/noAvatar.png')} alt="" className="topbarImg" />
                </Link>
            </div>
        </div>
    )
}

