import axios from "axios"
import { io } from "socket.io-client"
import { storageService } from "../services/storage.service"

const BASE_URL = process.env.NODE_ENV === 'production'
    ? '/api/'
    : '//localhost:8800/api/'
   


async function getUser(userId) {
    const user = await axios.get(`${BASE_URL}user/${userId}`)
    return user.data
}
async function getAllUsers() {
    const users = await axios.get(`${BASE_URL}user/all`)
    return users.data
}

async function followUser(userId, followerId) {
    await axios.put(`${BASE_URL}user/${userId}/follow`, { userId: followerId })
}

async function unfollowUser(userId, unfollowerId) {
    await axios.put(`${BASE_URL}user/${userId}/unfollow`, { userId: unfollowerId })
}

async function getUserFriends(user) {
    const friends = await axios.get(`${BASE_URL}user/friends/${user?._id}`)
    return friends.data
}
async function loginUser(userCredentials) {
    const user = await axios.post(`${BASE_URL}auth/login`, userCredentials)
    storageService.saveToStorage('user', user.data)
    return user.data
}
async function registerUser(miniUser) {
    const user = await axios.post(`${BASE_URL}auth/register`, miniUser)
    return user.data
}

async function sendLikeNotification(userId, details) {
    const notification = await axios.post(`${BASE_URL}user/notification/like/${userId}`, details)
    return notification.data
}
async function sendFollowNotification(userId, details) {
    const notification = await axios.post(`${BASE_URL}user/notification/follow/${userId}`, details)
    return notification.data
}
async function clearNotifications(userId, type) {
    try{
        await axios.delete(`${BASE_URL}user/notification/${userId}/${type}`)
    }catch(err){
        console.log(err);
    }
    
}
async function setProfilePicture(userId, url) {
    try{
        await axios.put(`${BASE_URL}user/${userId}/setprofile`, {url})
    }catch(err){
        console.log(err);
    }
    
}
async function setCoverPicture(userId, url) {
    try{
        await axios.put(`${BASE_URL}user/${userId}/setcover`, {url})
    }catch(err){
        console.log(err);
    }
    
}





export const userService = {
    getUserFriends,
    getUser,
    getAllUsers,
    loginUser,
    registerUser,
    followUser,
    unfollowUser,
    sendLikeNotification,
    sendFollowNotification,
    clearNotifications,
    setProfilePicture,
    setCoverPicture,
    
}