import React, { useEffect, useState } from 'react'
import {userService} from '../services/user.service'
import {format} from 'timeago.js'
export default function Comment({c}) {

    const [user, setUser] = useState(null)

    useEffect(()=>{
        getUser()
    },[c])

    const getUser = async() => {
        const user = await userService.getUser(c.userId)
        setUser(user)
    }

    return (
        <div className='comment'>
            <div className='commentImgContainer'>
                <img className='commentImg' src={user?.profilePicture || require('../assets/person/noAvatar.png')} alt="" />
            </div>
            <div className='commentsDetails'>
                <p className='commentUsername'>{user?.username}</p>
                <p className='commentText'>{c.text}</p>
                <p className='time'>{format(c.timeStamp)}</p>
            </div>
        </div>
    )
}
