import axios from "axios"
import { useContext } from "react"
import { postService } from "../services/post.service"
import { storageService } from "../services/storage.service"
import { userService } from "../services/user.service"
import { AuthContext } from "./AuthContext"


export const login = async (userCredentials, dispatch) => {
        dispatch({ type: "LOGIN_START" })
    try {
        const user = await userService.loginUser(userCredentials)
        const timeLine = await postService.getTimeline(user._id)
        dispatch({
            type: "LOGIN_SUCCESS",
            payload: user
        })

        // dispatch({
        //     type: "SET_POSTS",
        //     payload: timeLine
        // })

    } catch (err) {
        console.log(err);
        dispatch({
            type: "LOGIN_FAILURE",
            payload: err.response.data
        })
    }
}

export const follow = async(userId, followerId, dispatch) => {
    await userService.followUser(userId, followerId)
    dispatch({type: "FOLLOW", payload: userId})
}

export const unfollow = async(userId, followerId, dispatch) => {
    await userService.unfollowUser(userId, followerId)
    dispatch({type: "UNFOLLOW", payload: userId})
}

export const setUser = async(userId, dispatch) => {
    const user = await userService.getUser(userId)
    dispatch({type: "SET_USER", payload: user})
}
export const setUserFriends = async(user, dispatch) => {
    const friends = await userService.getUserFriends(user)
    dispatch({type: "SET_USER_FRIENDS", payload: friends})
}


export const sendNotification = async(type, userId, details, dispatch, user) => {
    let action
    switch (type) {
        case 'likes':
            //Push / Pull notification accordingly
            action = await userService.sendLikeNotification(userId, details)
            break;
        case 'newFollows':
            action = await userService.sendFollowNotification(userId, details)
            break;
    
        default:
            break;
    }  
    
}


export const markAsRead = async(dispatch, userId, propery) => {
    
    await userService.clearNotifications(userId, propery)

    console.log(propery);

    dispatch({type: "CLEAR_NOTIFICATIONS", payload: propery})
   

}

