import axios from "axios"

const BASE_URL = process.env.NODE_ENV === 'production'
    ? '/api/'
    : '//localhost:8800/api/'
   
 

async function getProfilePosts(userId){
    const posts = await axios.get(`${BASE_URL}post/profile/${userId}`)
    return posts.data
}

async function getPost(postId){
    const post = await axios.get(`${BASE_URL}post/${postId}`)
    return post.data
}

async function getTimeline(userId){
    const posts = await axios.get(`${BASE_URL}post/timeline/${userId}`)
    return posts.data
}
async function likePost(postId, userId){
    await axios.put(`${BASE_URL}post/${postId}/like`, {userId})
}
async function uploadPost(post){
    const savedPost = await axios.post(`${BASE_URL}post`, post)
    return savedPost.data
}

async function addPostComment(postId, comment) {
    try{
        await axios.put(`${BASE_URL}post/${postId}/addcomment`, {comment})
    }catch(err){
        console.log(err);
    }
    
}

export const postService = {
    getProfilePosts,
    getTimeline,
    likePost,
    uploadPost,
    getPost,
    addPostComment
}