import React, { useContext, useEffect, useState } from 'react'
import Topbar from '../components/Topbar'
import Rightbar from '../components/Rightbar'
import Sidebar from '../components/Sidebar'
import { userService } from '../services/user.service'

import { AuthContext } from '../context/AuthContext'
import SearchedFriend from '../components/SearchedFriend'
export default function FindFriends() {

    const {user:loggedInUser} = useContext(AuthContext)
    const [users, setUsers] = useState(null)
    const [usersToShow, setUsersToShow] = useState(null)


    useEffect(() => {
        getAllUsers()
        window.scrollTo(0, 0)

    }, [])

    const getAllUsers = async () => {
        let users = await userService.getAllUsers()
        console.log(users);
        users = users.filter(u=>u._id !== loggedInUser._id)
        setUsers(users)
        setUsersToShow(users)
    }


    return (
        <div className='findFriends'>
            {users && <Topbar users={[...users]} setUsersToShow={setUsersToShow}  />}
            <Sidebar className="sidebar"/>
            <div className="friendList">
                <h1>Discover people you may know:</h1>
                {usersToShow?.map(user => (
                   <SearchedFriend user={user}/>
                ))}
            </div>
            <Rightbar className="rightbar" />
        </div>
    )
}
