import React, { useContext, useEffect, useRef, useState } from 'react'
import MoreVertIcon from '@mui/icons-material/MoreVert'
import profileImg from "../assets/person/1.jpeg"
import postImg from "../assets/post/1.jpeg"
import likeImg from "../assets/like.png"
import heartImg from "../assets/heart.png"
import { Users } from '../dummyData'
import axios from 'axios'
import { format } from 'timeago.js'
import { Link } from 'react-router-dom'
import { color } from '@mui/system'
import { AuthContext } from '../context/AuthContext'
import { postService } from '../services/post.service'
import { userService } from '../services/user.service'
import { sendNotification } from '../context/AuthActions'
import { io } from 'socket.io-client'
import GoogleMap from './GoogleMap'
import { userSocket } from '../services/socket'
import CommentInput from './CommentInput'
import PostComments from './PostComments'

export default function Post({ postProp }) {

    // const user = Users.find(user => user.id === post.userId)
    const { user: loggedInUser, dispatch } = useContext(AuthContext)
    const [post, setPost] = useState(null)
    const [user, setUser] = useState({})
    const [like, setLike] = useState(0)
    const [isComments, setIsComments] = useState(false)
    const [isLiked, setIsLiked] = useState(post?.likes.includes(loggedInUser._id))

    const socket = useRef()
    useEffect(() => {
        socket.current = userSocket
        //   socket.current.emit('addUser', loggedInUser._id)

    }, [])


    useEffect(() => {
        if(postProp){
            setPost(postProp)
        }

    }, [postProp])

    useEffect(() => {
        if(post?._id){
          getUser()
        //   setLike(post?.likes.length)
        //   console.log(post.likes.length);
        //   setIsLiked(post.likes.includes(user._id))
      }
    }, [post])

    useEffect(() => {
        if(post?._id && user){
          setLike(post?.likes.length)
        
          setIsLiked(post.likes.includes(loggedInUser._id))
      }
    }, [post?.likes])

    const getUser = async () => {
        const user = await userService.getUser(post?.userId)
        setUser(user)
    }

    const likeHandler = async () => {
        //Server
        try {
            console.log('called');
            await postService.likePost(post?._id, loggedInUser._id)
            await sendNotification('likes', post?.userId, { userId: loggedInUser._id, postId: post?._id, timeStamp: new Date().toISOString() }, dispatch)
        } catch (err) {
            console.log(err);
        }
        //Client

        setLike(isLiked ? like - 1 : like + 1)
        setIsLiked(!isLiked)


        //Socket

        if (!isLiked) {
            console.log('emmited');
            socket?.current?.emit("like", { likerId: loggedInUser._id, likedId: user._id, postId: post?._id, timeStamp: new Date().toISOString() })
        } else {
            // dispatch({type: "REMOVE_NOTIFICATION", payload: {type: 'likes', details}})
        }


    }

    const addComment = (comment) => {

    }

    return (
        <div className="post">
            <div className="postWrapper">
                <div className="postTop">
                    <div className="postTopLeft" >
                        <Link to={`/profile/${user?._id}`} style={{ textDecoration: 'none', color: 'inherit' }}>
                            <img
                                src={(user?.profilePicture?.includes('../asstes')) ? require(user?.profilePicture) : user?.profilePicture || require('../assets/person/noAvatar.png')}
                                className="postProfileImg"
                                alt=""
                            />
                        </Link>
                        <span className="postUsername">
                            {user?.username}
                        </span>
                        <span className="postDate">{format(post?.createdAt)}</span>
                    </div>
                    <div className="postTopRight">
                        <MoreVertIcon />
                    </div>
                </div>
                <div className="postCenter">
                    <span className="postText">{post?.desc}</span>
                    <img className="postImg" src={post?.img} alt="" />
                    {post?.map ? <GoogleMap center={post?.map} /> : ''}
                </div>
                <div className="postBottom">
                    <div className="postBottomTop">
                        <div className="postBottomLeft">
                            <img className="likeIcon" src={likeImg} alt="" onClick={likeHandler} />
                            <img className="likeIcon" src={heartImg} alt="" onClick={likeHandler} />
                            <span className="postLikeCounter">{like} {like === 1 ? 'person' : 'people'} liked this</span>
                        </div>
                        <div className="postBottomRight">
                            <span className="postCommentText" onClick={()=>setIsComments(!isComments)}>{post?.comments?.length} comments</span>
                        </div>
                    </div>
                    <hr />
                    <CommentInput postId={post?._id} post={{...post}} setPost={setPost}/>
                    {isComments ? <PostComments comments={post?.comments}/> : ''}
                </div>
            </div>
        </div>
    )
}
