import React, { useContext, useEffect, useRef, useState } from 'react'
import ChatOnline from '../components/ChatOnline'
import Conversation from '../components/Conversation'
import Message from '../components/Message'
import Topbar from '../components/Topbar'
import { AuthContext } from '../context/AuthContext'
import { conversationService } from '../services/conversation.service'
import { messageService } from '../services/message.service'
import {io} from "socket.io-client"
import { userSocket } from '../services/socket'
import { userService } from '../services/user.service'
import MenuIcon from '@mui/icons-material/Menu';

const mapApi = '592d51682f56991d7299003310f4640a'
export default function Messenger() {

  const { user } = useContext(AuthContext)

  const [conversations, setConversations] = useState([])
  const [currentChat, setCurrentChat] = useState(null)
  const [messages, setMessages] = useState([])
  const [newMessage, setNewMessage] = useState('')
  const [arrivalMessage, setArrivalMessage] = useState(null)
  const [onlineUsers, setOnlineUsers] = useState([])
  const [friends, setFriends] = useState([])
  const [showContacts, setShowContacts] = useState(false)

  const socket = useRef()
  const scrollRef = useRef()
  const txt = useRef()

  useEffect(()=>{
    console.log('running');
    socket.current = userSocket
    socket.current.on("getMessage" , data =>{
      setArrivalMessage({
          sender: data.senderId,
          text: data.text,
          createdAt: Date.now()
      })
    })

  },[])

  useEffect(()=> {
    arrivalMessage && currentChat?.members.includes(arrivalMessage?.sender) && setMessages([...messages, arrivalMessage])
  },[arrivalMessage, currentChat])

  useEffect(()=>{
    // socket.current.emit('addUser', user._id)
    socket.current.on('getUsers', users => {
      // setOnlineUsers(user.following.filter(friendId => users.some(currUser => currUser.userId !== friendId)))
      setOnlineUsers(users.map(obj => {
        if(obj.userId !== user._id){
          return obj.userId
        }
      }))
    })
  },[user, socket])

  useEffect(() => {
    // getConversations()
    if(user._id){
      getFriends()
    }
  }, [user])

  useEffect(() => {
    getMessages(currentChat?._id)
  }, [currentChat])

  useEffect(()=>{
    scrollRef?.current?.scrollIntoView({behavior: "smooth"})
  },[messages])


  const getConversations = async () => {
    const conversations = await conversationService.getUsersConversations(user?._id)
    setConversations(conversations)
  }

  const getFriends = async() => {
    const friends = await userService.getUserFriends(user)
    // friends.map(async friend=> await userService.getUser(friend._id))
    setFriends(friends)
  }

  const getMessages = async (conversationId) => {
    const messages = await messageService.getConversationsMessages(conversationId)
    setMessages(messages)
  }

  const handleSubmit = async (ev) => {
    ev.preventDefault()
    const message = {
      sender: user._id,
      text: newMessage,
      conversationId: currentChat._id
    }

    const receiverId = currentChat?.members.find(id=> user._id !== id)
    socket.current.emit('sendMessage', {senderId: user._id, receiverId, text: newMessage})
    //For notification
    socket.current.emit('message', {senderId: user._id, receiverId, text: newMessage})

    const savedMessage = await messageService.addMessage(message)
    setMessages((prev) => [...prev, savedMessage])
    setNewMessage('')
    // txt.current = ''
  }

  const handleCurrChat = async(friendId) => {
    const myConversations = await conversationService.getUsersConversations(user._id)
    let conversation = myConversations.find(c=> c.members.includes(friendId))
    if(!conversation){
      conversation = await conversationService.addNewConversation([friendId, user._id])
    }

    setCurrentChat(conversation)

  }


  return (
    <>
      <Topbar chat/>
      <div className='messenger'>
        <div className={`chatMenu ${showContacts ? 'shown' : ''}`}>
          <div className="chatMenuWrapper">
            <input placeholder='Search for friends' className='chatMenuInput' type="text" name="" id="" />
            {friends?.map(conversation => (
              <div key={conversation._id} onClick={() => setCurrentChat(conversation)}>
                {/* <Conversation userId={conversation?.members?.filter(id=> user._id !== id)} /> */}
              </div>
            ))}
            {friends.map(friend=>(
              <div className="conversation" onClick={()=>handleCurrChat(friend._id)}>
                <img src={friend.profilePicture} className="conversationImg" alt="" />
                <p className='conversationName'>{friend.username}</p>
              </div>
            ))}
          </div>
        </div>
        <button className='menuBtn' onClick={()=>setShowContacts(!showContacts)}><MenuIcon/></button>
        <div className="chatBox">
          <div className="chatBoxWrapper">
            <div className="chatBoxTop">
              {messages?.map(message => (
                <div key={message._id} ref={scrollRef}>
                  <Message  message={message} own={message.sender === user._id} />
                </div>
              ))}
            </div>
            <div className="chatBoxBottom">
              <textarea onChange={(ev) => setNewMessage(ev.target.value)} value={newMessage} className='chatMessageInput' placeholder='Write something...'></textarea>
              <button className='chatSubmitButton' onClick={handleSubmit} >Send</button>
            </div>
          </div>
        </div>
      
        <div className="chatOnline">
          <div className="chatOnlineWrapper">
            <ChatOnline onlineUsers={onlineUsers} user={user} setCurrentChat={setCurrentChat}/>
          </div>
        </div>
      </div>
    </>
  )
}
