import axios from "axios";

const BASE_URL = process.env.NODE_ENV === 'production'
    ? '/api/'
    : '//localhost:8800/api/'
   


async function addNewConversation(members){
    try{
        const conversation = await axios.post(`${BASE_URL}conversation/`, members)
        return conversation.data
    }catch(err){
        console.log(err);
    }
}
async function getUsersConversations(userId){
    try{
        const conversations = await axios.get(`${BASE_URL}conversation/${userId}`)
        return conversations.data
    }catch(err){
        console.log(err);
    }
}

async function findConversationByIds(firstId, secondId){
    try{
        const conversation = await axios.get(`${BASE_URL}conversation/find/${firstId}/${secondId}`)
        return conversation.data
    }catch(err){
        console.log(err);
    }
}


export const conversationService = {
    getUsersConversations,
    findConversationByIds,
    addNewConversation
}