
import axios from "axios";

const BASE_URL = process.env.NODE_ENV === 'production'
    ? '/api/'
    : '//localhost:8800/api/'
   


//Add new message

async function addMessage(message){
    try{
        const savedMassage = await axios.post(`${BASE_URL}message/`, message)
        return savedMassage.data
    }catch(err){
        console.log(err);
    }
}
//Get conversations's messages

async function getConversationsMessages(conversationId){
    try{
        const messages = await axios.get(`${BASE_URL}message/${conversationId}`)
        return messages.data
    }catch(err){
        console.log(err);
    }
}


export const messageService = {
    addMessage,
    getConversationsMessages,
}